/* Form Module Theme Variables - Store Theme Colors */

/* Light Theme - Orange (Store ) */
:root,
.light-theme {
  /* Form specific colors */
  --form-bg-primary: #ffffff;
  --form-bg-secondary: #fff8f0;
  --form-bg-tertiary: #ffedd5;
  --form-text-primary: #292524;
  --form-text-secondary: #78716c;
  --form-text-tertiary: #a8a29e;
  --form-border-color: #fed7aa;
  --form-shadow: rgba(255, 107, 53, 0.1);
  
  /* Primary colors - Orange theme (Store Primary) */
  --form-primary-color: #ff6b35;
  --form-primary-dark: #e55a2b;
  --form-primary-light: #ff8746;
  --form-primary-lighter: #ffa366;
  --form-accent-primary: #e65100;
  --form-accent-secondary: #ff6b35;
  
  /* Field colors */
  --form-field-bg: #ffffff;
  --form-field-border: #fed7aa;
  --form-field-focus: #ff6b35;
  --form-field-hover: #ffedd5;
  --form-field-disabled: #fafaf9;
  
  /* Button colors */
  --form-button-bg: #ff6b35;
  --form-button-hover: #e55a2b;
  --form-button-text: #ffffff;
  
  /* Status colors */
  --form-success-color: #16a34a;
  --form-success-light: #dcfce7;
  --form-warning-color: #eab308;
  --form-warning-light: #fef9c3;
  --form-error-color: #dc2626;
  --form-error-light: #fee2e2;
  --form-info-color: #0ea5e9;
  --form-info-light: #e0f2fe;
  
  /* Gradients */
  --form-gradient-primary: linear-gradient(135deg, #e65100 0%, #ff6b35 50%, #ff8746 100%);
  --form-gradient-secondary: linear-gradient(135deg, #fff8f0 0%, #ffedd5 100%);
  --form-gradient-accent: linear-gradient(135deg, #ff8746 0%, #ff6b35 100%);
  --form-gradient-warm: linear-gradient(135deg, #ff6b35 0%, #e65100 100%);
}

/* Dark Theme - Green/Teal (Store ) */
.dark-theme {
  /* Form specific colors */
  --form-bg-primary: #0f172a;
  --form-bg-secondary: #1e293b;
  --form-bg-tertiary: #334155;
  --form-text-primary: #f1f5f9;
  --form-text-secondary: #cbd5e1;
  --form-text-tertiary: #94a3b8;
  --form-border-color: #475569;
  --form-shadow: rgba(0, 212, 170, 0.2);
  
  /* Primary colors - Green/Teal theme (Store Primary) */
  --form-primary-color: #00d4aa;
  --form-primary-dark: #00b894;
  --form-primary-light: #00e5bf;
  --form-primary-lighter: #33eac8;
  --form-accent-primary: #00d4aa;
  --form-accent-secondary: #00b894;
  
  /* Field colors */
  --form-field-bg: #1e293b;
  --form-field-border: #475569;
  --form-field-focus: #00d4aa;
  --form-field-hover: #334155;
  --form-field-disabled: #0f172a;
  
  /* Button colors */
  --form-button-bg: #00d4aa;
  --form-button-hover: #00b894;
  --form-button-text: #ffffff;
  
  /* Status colors */
  --form-success-color: #22c55e;
  --form-success-light: #14532d;
  --form-warning-color: #fbbf24;
  --form-warning-light: #451a03;
  --form-error-color: #ef4444;
  --form-error-light: #450a0a;
  --form-info-color: #06b6d4;
  --form-info-light: #164e63;
  
  /* Gradients */
  --form-gradient-primary: linear-gradient(135deg, #00d4aa 0%, #00b894 100%);
  --form-gradient-secondary: linear-gradient(135deg, #1e293b 0%, #334155 100%);
  --form-gradient-accent: linear-gradient(135deg, #00e5bf 0%, #00d4aa 100%);
  --form-gradient-cool: linear-gradient(135deg, #00d4aa 0%, #00b894 100%);
}

/* Smooth theme transitions */
.form-builder-container,
.form-test-container,
.mat-mdc-card,
.mat-mdc-form-field {
  transition: background-color 0.3s ease, color 0.3s ease, border-color 0.3s ease, box-shadow 0.3s ease;
}

/* Custom scrollbar for forms */
.light-theme {
  ::-webkit-scrollbar {
    width: 12px;
    height: 12px;
  }

  ::-webkit-scrollbar-track {
    background: var(--form-bg-secondary);
    border-radius: 10px;
  }

  ::-webkit-scrollbar-thumb {
    background: var(--form-gradient-primary);
    border-radius: 10px;
    border: 2px solid var(--form-bg-secondary);
    
    &:hover {
      border-width: 1px;
      box-shadow: 0 0 6px var(--form-shadow);
    }
  }
}

.dark-theme {
  ::-webkit-scrollbar {
    width: 12px;
    height: 12px;
  }

  ::-webkit-scrollbar-track {
    background: var(--form-bg-tertiary);
    border-radius: 10px;
  }

  ::-webkit-scrollbar-thumb {
    background: var(--form-gradient-primary);
    border-radius: 10px;
    border: 2px solid var(--form-bg-tertiary);
    
    &:hover {
      border-width: 1px;
      box-shadow: 0 0 8px var(--form-shadow);
    }
  }
}

/* Selection colors */
.light-theme ::selection {
  background: var(--form-primary-color);
  color: white;
}

.dark-theme ::selection {
  background: var(--form-primary-color);
  color: white;
}

/* Material Form Field theming */
.light-theme {
  .mat-mdc-form-field {
    --mdc-outlined-text-field-container-shape: 8px;
    --mdc-outlined-text-field-outline-color: var(--form-field-border);
    --mdc-outlined-text-field-focus-outline-color: var(--form-primary-color);
    --mdc-outlined-text-field-hover-outline-color: var(--form-primary-light);
    --mdc-filled-text-field-container-color: var(--form-field-bg);
    --mat-form-field-container-text-color: var(--form-text-primary);
  }

  .mat-mdc-input-element {
    color: var(--form-text-primary);
    caret-color: var(--form-primary-color);
  }

  .mat-mdc-form-field-error {
    color: var(--form-error-color);
  }
}

.dark-theme {
  .mat-mdc-form-field {
    --mdc-outlined-text-field-container-shape: 8px;
    --mdc-outlined-text-field-outline-color: var(--form-field-border);
    --mdc-outlined-text-field-focus-outline-color: var(--form-primary-color);
    --mdc-outlined-text-field-hover-outline-color: var(--form-primary-light);
    --mdc-filled-text-field-container-color: var(--form-field-bg);
    --mat-form-field-container-text-color: var(--form-text-primary);
  }

  .mat-mdc-input-element {
    color: var(--form-text-primary);
    caret-color: var(--form-primary-color);
  }

  .mat-mdc-form-field-error {
    color: var(--form-error-color);
  }
}

/* Material Card theming */
.light-theme {
  .mat-mdc-card {
    background-color: var(--form-bg-primary);
    color: var(--form-text-primary);
    border: 1px solid var(--form-border-color);
    box-shadow: 0 2px 8px var(--form-shadow);
    
    &:hover {
      box-shadow: 0 4px 16px var(--form-shadow);
    }
  }

  .mat-mdc-card-header {
    border-bottom: 1px solid var(--form-border-color);
  }

  .mat-mdc-card-title {
    color: var(--form-text-primary);
  }
}

.dark-theme {
  .mat-mdc-card {
    background-color: var(--form-bg-secondary);
    color: var(--form-text-primary);
    border: 1px solid var(--form-border-color);
    box-shadow: 0 4px 12px var(--form-shadow);
    
    &:hover {
      box-shadow: 0 6px 20px var(--form-shadow);
    }
  }

  .mat-mdc-card-header {
    border-bottom: 1px solid var(--form-border-color);
  }

  .mat-mdc-card-title {
    color: var(--form-text-primary);
  }
}

/* Material Button theming */
.light-theme {
  .mat-mdc-raised-button.mat-primary,
  .mat-mdc-unelevated-button.mat-primary,
  .mat-mdc-fab.mat-primary {
    background: linear-gradient(135deg, #f97316 0%, #ea580c 100%) !important;
    color: white !important;
    box-shadow: 0 2px 8px rgba(251, 146, 60, 0.3);
    border: none !important;
    
    &:hover:not(:disabled) {
      opacity: 0.9;
      box-shadow: 0 4px 16px rgba(251, 146, 60, 0.4);
    }
    
    &:active:not(:disabled) {
      opacity: 0.85;
    }
  }

  .mat-mdc-raised-button.mat-accent {
    background: linear-gradient(135deg, #ffb703 0%, #fb8500 100%) !important;
    color: white !important;
    box-shadow: 0 2px 8px rgba(251, 133, 0, 0.3);
  }

  .mat-mdc-button.mat-primary {
    color: #f97316 !important;
  }

  .mat-mdc-icon-button {
    color: #78716c;
    
    &:hover {
      background-color: #ffedd5;
    }
  }
}

.dark-theme {
  .mat-mdc-raised-button.mat-primary,
  .mat-mdc-unelevated-button.mat-primary,
  .mat-mdc-fab.mat-primary {
    background: linear-gradient(135deg, #10b981 0%, #059669 100%) !important;
    color: white !important;
    box-shadow: 0 4px 12px rgba(16, 185, 129, 0.4);
    border: none !important;
    
    &:hover:not(:disabled) {
      opacity: 0.9;
      box-shadow: 0 6px 20px rgba(16, 185, 129, 0.5);
    }
    
    &:active:not(:disabled) {
      opacity: 0.85;
    }
  }

  .mat-mdc-raised-button.mat-accent {
    background: linear-gradient(135deg, #14b8a6 0%, #0d9488 100%) !important;
    color: white !important;
    box-shadow: 0 4px 12px rgba(20, 184, 166, 0.4);
  }

  .mat-mdc-button.mat-primary {
    color: #10b981 !important;
  }

  .mat-mdc-icon-button {
    color: #cbd5e1;
    
    &:hover {
      background-color: #334155;
    }
  }
}

/* Material Radio Button theming */
.light-theme {
  .mat-mdc-radio-button {
    --mdc-radio-selected-icon-color: var(--form-primary-color);
    --mdc-radio-selected-hover-icon-color: var(--form-primary-light);
    --mdc-radio-selected-pressed-icon-color: var(--form-primary-dark);
    --mat-radio-ripple-color: var(--form-primary-color);
  }
}

.dark-theme {
  .mat-mdc-radio-button {
    --mdc-radio-selected-icon-color: var(--form-primary-color);
    --mdc-radio-selected-hover-icon-color: var(--form-primary-light);
    --mdc-radio-selected-pressed-icon-color: var(--form-primary-dark);
    --mat-radio-ripple-color: var(--form-primary-color);
  }
}

/* Material Select theming */
.light-theme {
  .mat-mdc-select {
    --mat-select-trigger-text-color: var(--form-text-primary);
    --mat-select-enabled-arrow-color: var(--form-primary-color);
  }
}

.dark-theme {
  .mat-mdc-select {
    --mat-select-trigger-text-color: var(--form-text-primary);
    --mat-select-enabled-arrow-color: var(--form-primary-color);
  }
}

/* Material Datepicker theming */
.light-theme {
  .mat-datepicker-content {
    background-color: var(--form-bg-primary);
    color: var(--form-text-primary);
    
    .mat-calendar {
      background-color: var(--form-bg-primary);
    }
    
    .mat-calendar-body-selected {
      background-color: var(--form-primary-color);
      color: white;
    }
    
    .mat-calendar-body-today {
      border-color: var(--form-primary-color);
    }
  }
}

.dark-theme {
  .mat-datepicker-content {
    background-color: var(--form-bg-secondary);
    color: var(--form-text-primary);
    
    .mat-calendar {
      background-color: var(--form-bg-secondary);
    }
    
    .mat-calendar-body-selected {
      background-color: var(--form-primary-color);
      color: white;
    }
    
    .mat-calendar-body-today {
      border-color: var(--form-primary-color);
    }
  }
}
