// ========================================
// CSS     ()
// ========================================
:root {
  //   ()
  --bg-primary: #ffffff;
  --bg-secondary: #f8f9fa;
  --bg-tertiary: #e9ecef;
  --bg-accent: #e3f2fd;
  --bg-warning: #fff3e0;
  --text-primary: #212529;
  --text-secondary: #495057;
  --text-muted: #6c757d;
  --border-color: #dee2e6;
  --input-bg: #ffffff;
  --shadow: rgba(0, 0, 0, 0.1);
  --accent-primary: #007bff;
  --accent-color: #1976d2;
  --warning-color: #f57c00;
  --accent-secondary: #6c757d;
  --success: #28a745;
  --warning: #ffc107;
  --danger: #dc3545;
  --info: #17a2b8;
  --gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  --gradient-secondary: linear-gradient(135deg, #f093fb 0%, #f5576c 100%);

  // Slack   (  - )
  --slack-primary: #ff6b35;
  --slack-primary-dark: #e55a2b;
  --slack-primary-light: rgba(255, 107, 53, 0.1);
  --slack-gradient: linear-gradient(135deg, #ff6b35 0%, #e55a2b 100%);
  --slack-gradient-hover: linear-gradient(135deg, #e55a2b 0%, #d14a1e 100%);
  --slack-shadow: rgba(255, 107, 53, 0.3);
}

