//   - Body   
.logout-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10000; // UI chrome(1000)  ,   
  display: flex;
  align-items: center;
  justify-content: center;

  .modal-backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(2px);
  }

  .modal-content {
    position: relative;
    background: var(--bg-primary);
    border-radius: 12px;
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
    max-width: 400px;
    width: 90%;
    animation: modalFadeIn 0.2s ease-out;
    z-index: 1;
  }

  .modal-header {
    padding: 20px 24px;
    border-bottom: 1px solid var(--border-color);

    h3 {
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      color: var(--text-primary);
    }
  }

  .modal-body {
    padding: 24px;

    p {
      margin: 0 0 12px 0;
      font-size: 15px;
      color: var(--text-primary);
      line-height: 1.5;
    }

    .modal-description {
      font-size: 13px;
      color: var(--text-secondary);
      margin: 0;
    }
  }

  .modal-footer {
    padding: 16px 24px;
    border-top: 1px solid var(--border-color);
    display: flex;
    justify-content: flex-end;
    gap: 8px;

    button {
      padding: 8px 20px;
      border-radius: 6px;
      font-size: 14px;
      font-weight: 500;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &.btn-cancel {
        background: var(--bg-secondary);
        color: var(--text-primary);
        border-color: var(--border-color);

        &:hover {
          background: var(--bg-tertiary);
          border-color: var(--primary-color);
        }
      }

      &.btn-confirm {
        background: #dc2626;
        color: white;
        border-color: #dc2626;

        &:hover {
          background: #b91c1c;
          box-shadow: 0 2px 8px rgba(220, 38, 38, 0.3);
        }

        &:active {
          background: #991b1b;
        }
      }
    }
  }

  @keyframes modalFadeIn {
    from {
      opacity: 0;
      transform: scale(0.95) translateY(-10px);
    }
    to {
      opacity: 1;
      transform: scale(1) translateY(0);
    }
  }
}

