          }
        }
      }
    }

    // Submenu items
    .mat-mdc-menu-item {
      color: #f1f5f9 !important;
      mat-icon { color: #cbd5e1 !important; }
      span { color: #f1f5f9 !important; }
      
      &:hover {
        background: rgba(255, 255, 255, 0.1) !important;
        mat-icon, span { color: #818cf8 !important; }
      }
    }

    .mat-mdc-menu-submenu-icon {
      fill: #cbd5e1 !important;
    }
  }
}

// ========================================
//  Angular Material Menu - Base Styles (Education)
// ========================================
.user-menu-panel, .mode-menu, .login-type-menu {
  border-radius: 12px !important;
  overflow: hidden !important;
  margin-top: 8px !important;

  .mat-mdc-menu-content {
    padding: 0 !important;
    min-width: 240px !important;
  }

  .user-menu-header {
    padding: 20px 16px !important;
    background: #f8f9fa;
    border-bottom: 1px solid #eee;

    .menu-user-info {
      display: flex;
      flex-direction: column;
      gap: 2px;

      .menu-user-name {
        font-weight: 700 !important;
        color: #1d1d1f;
        font-size: 16px !important;
      }

      .menu-user-email {
        font-size: 13px !important;
        color: #666;
      }
    }
  }

  .mat-divider {
    margin: 0 !important;
  }

  .menu-actions, .mat-mdc-menu-content {
    padding: 8px !important;

    .mat-mdc-menu-item {
      height: 44px !important;
      border-radius: 8px !important;
      margin-bottom: 2px !important;
