// ========================================
//       
// ========================================
[data-theme="dark"],
.dark-theme,
body.dark-theme,
body.dark-mode {
  // Material MDC Input Field -    
  .mdc-text-field__input {
    color: #f1f5f9 !important;
    caret-color: #60a5fa !important;
  }

  //  input/textarea 
  input:not([type="checkbox"]):not([type="radio"]):not([type="button"]):not([type="submit"]):not([type="reset"]) {
    color: #f1f5f9 !important;
    caret-color: #60a5fa !important;
    
    &::placeholder {
      color: #94a3b8 !important;
    }
  }

  textarea {
    color: #f1f5f9 !important;
    caret-color: #60a5fa !important;
    
    &::placeholder {
      color: #94a3b8 !important;
    }
  }

  // Material Form Field Wrapper
  .mat-mdc-text-field-wrapper {
    input,
    textarea {
      color: #f1f5f9 !important;
      caret-color: #60a5fa !important;
    }
  }

  // Material Form Field -  
  .mat-mdc-form-field {
    .mat-mdc-input-element {
      color: #f1f5f9 !important;
      caret-color: #60a5fa !important;
    }

    // Focus 
    .mdc-text-field--focused {
      .mat-mdc-input-element,
      .mdc-text-field__input {
        color: #f1f5f9 !important;
      }
    }
  }

  // Material input (Filled)
  .mat-mdc-form-field.mat-form-field-appearance-fill {
    .mdc-text-field__input {
      color: #f1f5f9 !important;
    }
  }

  // Material input (Outline)
  .mat-mdc-form-field.mat-form-field-appearance-outline {
    .mdc-text-field__input {
      color: #f1f5f9 !important;
    }
  }

  // ========================================
  // Material Radio Button -  
  // ========================================
  .mat-mdc-radio-button {
    --mdc-radio-unselected-icon-color: #cbd5e1 !important;
    --mdc-radio-selected-icon-color: #60a5fa !important;
    
    color: #f1f5f9 !important;

    .mat-mdc-radio-label,
    .mdc-radio__label-content {
      color: #f1f5f9 !important;
    }

    span:not(.mdc-radio__background):not(.mdc-radio__native-control) {
      color: #f1f5f9 !important;
    }
  }

  // MDC Radio  
  .mdc-radio {
    .mdc-radio__native-control {
      border-color: #cbd5e1 !important;
      accent-color: #60a5fa !important;
    }

    .mdc-radio__background {
      border-color: #cbd5e1 !important;

      .mdc-radio__outer-circle {
        border-color: #cbd5e1 !important;
      }

      .mdc-radio__inner-circle {
        border-color: #60a5fa !important;
        background-color: #60a5fa !important;
      }
    }

    .mdc-radio__label-content {
      color: #f1f5f9 !important;
    }
  }

  // Radio Button  
  .mdc-radio .mdc-radio__native-control:checked + .mdc-radio__background {
    border-color: #60a5fa !important;

    .mdc-radio__outer-circle {
      border-color: #60a5fa !important;
    }

    .mdc-radio__inner-circle {
      background-color: #60a5fa !important;
      border-color: #60a5fa !important;
    }
  }

  // Radio Button  
  .mdc-radio:hover .mdc-radio__background {
    border-color: #e2e8f0 !important;
  }

  //    
  mat-radio-group .mat-mdc-radio-button {
    color: #f1f5f9 !important;

    .mat-mdc-radio-label {
      color: #f1f5f9 !important;
    }

    .mdc-radio__background {
      border-color: #cbd5e1 !important;
    }

    .mdc-radio__native-control:checked + .mdc-radio__background {
      border-color: #60a5fa !important;
    }
  }

  //    
  .mat-mdc-radio-button .mat-radio-label-content {
    color: #f1f5f9 !important;
  }

  // ========================================
  // Material Checkbox -  
  // ========================================
  .mat-mdc-checkbox {
    --mdc-checkbox-unselected-outline-color: #cbd5e1 !important;
    --mdc-checkbox-selected-checkmark-color: #1e293b !important;
    
    .mat-mdc-checkbox-label {
      color: #f1f5f9 !important;
    }
  }

  // MDC Checkbox  
  .mdc-checkbox {
    .mdc-checkbox__native-control {
      border-color: #cbd5e1 !important;
      accent-color: #60a5fa !important;
    }

    .mdc-checkbox__background {
      border-color: #cbd5e1 !important;
      background-color: transparent !important;
    }

    .mdc-checkbox__checkmark {
      color: #1e293b !important;
    }
  }

  // Checkbox  
  .mdc-checkbox .mdc-checkbox__native-control:checked + .mdc-checkbox__background {
    background-color: #60a5fa !important;
    border-color: #60a5fa !important;

    .mdc-checkbox__checkmark {
      color: #1e293b !important;
    }
  }

  // Checkbox  
  .mdc-checkbox:hover .mdc-checkbox__background {
    border-color: #e2e8f0 !important;
  }
}
